
img{
  border-radius: 50%
}

input {
  margin: auto;
  float: none;
  font-size: 15px;
  width: 100%;
  height: 5%;
  max-width: 307px;
  padding: 5px 10px;

}
.header{
  height: 100px;
  width: 90%;
  margin: auto;
  background-color: rgba(255, 255, 255, 0.658);
  border-radius: 10px;
  padding: 10px;
  display: flex;
  font-size: 20px;
  font-style: oblique;
  position: static;
  font-size: 20px;
}

.friendlistcontent{
  height: 450px;
  width: 90%;
  margin: auto;
  background-color: rgba(255, 255, 255, 0.658);
  border-radius: 10px;
  padding: 10px;
  display: inline-block;
  font-size: 20px;
  font-style: oblique;
  position: static;
}

.friendlistbox{
  height: 50px;
  width: 90%;
  margin: 10px;
  background-color: rgba(253, 147, 147, 0.445);
  border-radius: 10px;
  padding: 10px;
  display: flex;
  font-size: 20px;
  font-style: oblique;
  position: static;
}

.bottom{
  height: 100px;
  width: 90%;
  margin: auto;
  background-color: rgba(255, 255, 255, 0.658);
  border-radius: 10px;
  padding: 10px;
  display: flex;
  font-size: 20px;
  font-style: oblique;
  position: static;
}




























